html {
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

body {
    background-color: var(--primary-bcgcolor);
}

ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

a {
    text-decoration: none;
}

button {
    font-family: inherit;
    cursor: pointer;
}

.theme-light {
    --primary-bcgcolor: #F4F4F4;
    --primary-color: #111321;
    --primary-textcolor: #111321;
    --accent-color: #4B48DB;
    --primary-iconscolor: #4B48DB;
    --slidebar-bcgcolor: #FFFFFF;
    --slider-bordercolor: #4B48DB;
    --themelight-namecolor: #4B48DB;
    --themedark-namecolor: #5F6775;
    --headerborder-color: rgba(0, 0, 0, 0.2);
}

.theme-dark {
    --primary-bcgcolor: #111321;
    --primary-color: #F4F4F4;
    --primary-textcolor: #F4F4F4;
    --accent-color: #4B48DB;
    --primary-iconscolor: #5F6775;
    --slidebar-bcgcolor: #4B48DB;
    --slider-bordercolor: #FFFFFF;
    --themelight-namecolor: #5F6775;
    --themedark-namecolor: #4B48DB;
    --headerborder-color: rgba(244, 244, 244, 0.2);
}

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }

.header-section {
    padding-top: 24px;
    padding-bottom: 22px;
    border: 1px solid var(--headerborder-color);

    @media screen and (min-width: $tablet) {
        padding-top: 32px;
        padding-bottom: 28px;
    }

    @media screen and (min-width: $desktop) {
        padding-top: 28px;
    }
}

.container {
    width: 320px;
    padding-left: 16px;
    padding-right: 16px;
    margin-left: auto;
    margin-right: auto;

    @media screen and (min-width: $tablet) {
        width: $tablet;
    }

    @media screen and (min-width: $desktop) {
        width: $desktop;
    }
}

.header-container {
    display: flex;
    align-items: baseline;
}

.logo {
    font-family: $main-font;
    font-weight: 700;
    font-size: 24px;
    line-height: 1.38;
    color: var(--primary-textcolor);
    margin-right: 14px;

    @media screen and (min-width: $tablet) {
        font-size: 32px;
        margin-right: 48px;
    }

    @media screen and (min-width: $desktop) {
        font-size: 42px;
        line-height: 1.36;
        margin-right: 102px;
    }
}

.site-nav {
    @media screen and (max-width: $mobile-end) {
        display: none;
    }

    @media screen and (min-width: $tablet) {
        margin-right: 32px;
    }

    @media screen and (min-width: $desktop) {
        margin-right: 147px;
    }
}

.nav-list {
    @media screen and (min-width: $tablet) {
        display: flex;
    }
}

.nav-list__item {
    @media screen and (min-width: $tablet) {
        font-family: $main-font;
        font-weight: 700;
        font-size: 16px;
        line-height: 1.38;
        color: var(--primary-textcolor);
    }

    @media screen and (min-width: $desktop) {
        font-size: 18px;
        line-height: 1.39;
    }
}

.nav-list__item:not(:last-child) {
    @media screen and (min-width: $tablet) {
        margin-right: 44px;
    }

    @media screen and (min-width: $desktop) {
        margin-right: 69px;
    }
}

.nav-list__link {
    @media screen and (min-width: $tablet) {
        color: var(--primary-textcolor);
        padding-top: 16px;
        padding-bottom: 8px;
    }

    @media screen and (min-width: $desktop) {
        padding-top: 27px;
        padding-bottom: 5px;
    }
}

.current {
    position: relative;
}

.current::after {
    @media screen and (min-width: $tablet) {
        position: absolute;
        left: 0;
        bottom: 0;
        content: "";
        width: 100%;
        border-bottom: 1px solid var(--accent-color);
    }
}

.search-form {
    position: relative;
}

.search-form__input {
    width: 173px;
    height: 31px;
    border: 1px solid var(--primary-color);
    border-radius: 20px;
    background-color:var(--primary-bcgcolor);
    padding-left: 48px;
    padding-right: 15px;
    font-family: $theme-switcher-font;
    font-weight: 400;
    font-size: 12px;
    line-height: 1.75;
    letter-spacing: 0.02em;
    color: var(--primary-color);

    @media screen and (min-width: $tablet) {
        width: 213px;
        font-size: 14px;
        line-height: 1.71;
    }

    @media screen and (min-width: $desktop) {
        width: 288px;
    }
}

.search-form__button {
    position: absolute;
    display: inline-block;
    top: 6px;
    left: 12px;
    border: 0;
    padding: 0;
    justify-content: center;
    background-color: transparent;
    color: var(--primary-color);
}

.search-form__icon {
    fill: currentColor;
}


.theme-switch {
    @media screen and (max-width: $mobile-end) {
        display: none;
    }

    @media screen and (min-width: $tablet) {
        margin-left: auto;
        display: flex;
        align-items: center;
        gap: 9px;
        color: var(--primary-iconscolor);
    }

    @media screen and (min-width: $desktop) {
        gap: 12px;
        font-family: $theme-switcher-font;
        font-weight: 400;
        font-size: 18px;
        line-height: 1.33;
    }
}

.theme-switch__lighticon {
    stroke: var(--themelight-namecolor);

    @media screen and (min-width: $desktop) {
        display: none;
    }
}

.theme-switch__darkicon {
    stroke: var(--themedark-namecolor);

    @media screen and (min-width: $desktop) {
        display: none;
    }
}

.theme-switch__light {
    @media screen and (max-width: $tablet-end) {
        display: none;
    }

    @media screen and (min-width: $desktop) {
    color: var(--themelight-namecolor);
    }
}

.theme-switch__dark {
    @media screen and (max-width: $tablet-end) {
        display: none;
    }

    @media screen and (min-width: $desktop) {
    color: var(--themedark-namecolor);
    }
}

.theme-switch__slider {
    position: relative;
    display: inline-block;
    cursor: pointer;
    width: 40px;
    height: 20px;
    background-color: var(--slidebar-bcgcolor);
    border: 0.604701px solid var(--slider-bordercolor);
    border-radius: 20px;
}

.theme-switch__slider::before {
    content: "";
    position: absolute;
    z-index: 10;
    top: 1px;
    left: 1px;
    width: 16px;
    height: 16px;
    background-color: var(--slider-bordercolor);
    border-radius: 50%;
    border: none;
}

.theme-switch__checkbox:checked + .theme-switch__slider::before {
    transform: translateX(20px);
}